import React from 'react'
import '../CSS/Home.css'
import Swal from 'sweetalert2';
{/* <scrit src="https://cdn.jsdelivr.net/npm/sweetalert2@11"></scrit> */}

const ShoppingCart = () => {
  const showPopup=()=>{
    Swal.fire({
      title: "Welcome to GeeksForGeeks!",
      text: "GeeksForGeeks is the best platform for Learning.",
      imageUrl: "https://placeholder.pics/svg/300x1500",
      imageWidth: 400,
      imageHeight: 200,
      showCloseButton: true,
  })
  }
  return (
   <>
   <div className='bodymydivPopup'>

   <h3>Create popup box using SweetAlert</h3>
        <button onClick={showPopup}>
            Show Popup
        </button>

   </div>


   </>
  )
}

export default ShoppingCart
